.item-container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  
  .time {
    /* font-size: 2rem; */
    /* padding: 1rem; */
  }

  .row {
    margin-left: auto;
  }
  
  .button {
    padding: .6rem 1.5rem;
    margin: 0rem .4rem;
    border-radius: 3px;
    text-transform: uppercase;
    font-weight: 600;
    font-size: .8rem;
    border-style: groove;
  }
  
  .button:focus {
    outline-width: 0;
  }
  
  .button-primary:hover {
    background-color: #2641d4;
    border: 1px solid #1b1f2b;
  }
  
  .button-primary-active {
    background-color: #3151ff;
    border: 1px solid #152684;
    color: white;
  }
  
  .button-primary-inactive {
    background-color: #3151ff;
    border: 1px solid #152684;
    color: white;
  }